@import url("https://fonts.googleapis.com/css2?family=Space+Mono:wght@400;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  background: #c5e4e7;
  font-family: "Space Mono", monospace;
}

a {
  text-decoration: none;
  cursor: pointer;
}

p {
  font-size: 1rem;
}

input,
button {
  font-size: 1.5rem;
}

ul {
  list-style-type: none;
}

.logo {
  max-width: 6.25rem;
  margin: 2.1875rem auto;
}
.logo img {
  width: 100%;
}

main {
  background: white;
  padding: 3.125rem;
  border-radius: 1.875rem 1.875rem 0 0;
  max-width: 23.4375rem;
  margin: auto;
}
@media (min-width: 64em) {
  main {
    max-width: 57.5rem;
    display: flex;
    gap: 3.125rem;
    border-radius: 1.875rem;
  }
}
@media (min-width: 64em) {
  main .input {
    width: 100%;
  }
}
main .input__title {
  display: block;
  margin-bottom: 2.5rem;
  font-weight: bold;
  color: #5e7a7d;
  position: relative;
}
main .input__title input {
  width: 100%;
  border: none;
  outline: none;
  font-family: "Space Mono", monospace;
  font-weight: bold;
  border-radius: 0.3125rem;
  text-align: center;
  cursor: pointer;
  caret-color: #26c0ab;
}
main .input__title .input__container {
  margin-top: 0.625rem;
  width: 100%;
  position: relative;
}
main .input__title .input__container::before {
  content: url(../../../images/icon-dollar.svg);
  position: absolute;
  top: 50%;
  left: 1.25rem;
  width: 0.75rem;
  height: 1.125rem;
  transform: translateY(-50%);
  pointer-events: none;
}
main .input__title .input__container.people::before {
  content: url(../../../images/icon-person.svg);
}
main .input__title .input__container input {
  text-align: right;
  background: #f4fafa;
  color: #00494d;
  padding: 0 1.25rem;
}
main .input__title .grid-container {
  margin-top: 0.625rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1.25rem;
}
@media (min-width: 64em) {
  main .input__title .grid-container {
    grid-template-columns: repeat(3, 1fr);
  }
}
main .input__title .grid-container .grid-item {
  background: #00494d;
  display: flex;
  text-align: center;
  border-radius: 0.3125rem;
}
main .input__title .grid-container .grid-item button {
  display: block;
  width: 100%;
  padding: 0.3125rem 0;
  color: white;
  font-family: "Space Mono", monospace;
  font-weight: bold;
  background: transparent;
  transition: 250ms ease-in-out;
  cursor: pointer;
  border-radius: 0.3125rem;
  border: none;
}
main .input__title .grid-container .grid-item button:hover, main .input__title .grid-container .grid-item button:focus, main .input__title .grid-container .grid-item button:active {
  background: #26c0ab;
  color: #00494d;
}
main .input__title .grid-container .grid-item button:focus-visible {
  outline: none;
}
main .input__title .grid-container .grid-item.custom {
  background: #f4fafa;
}
main .input__title .grid-container .grid-item.custom input {
  color: #00494d;
  background: inherit;
  transition: 250ms ease-in-out;
  outline: 1px solid transparent;
}
main .input__title .grid-container .grid-item.custom input::placeholder {
  color: #00494d;
  font-size: 1.25rem;
  text-align: right;
  padding-right: 1.25rem;
}
main .input__title .grid-container .grid-item.custom input:active, main .input__title .grid-container .grid-item.custom input:focus, main .input__title .grid-container .grid-item.custom input:hover {
  background: #f4fafa;
  outline: 1px solid #26c0ab;
}
main .output {
  background: #00494d;
  border-radius: 0.625rem;
  padding: 2.1875rem 1.25rem;
}
@media (min-width: 64em) {
  main .output {
    width: 100%;
  }
}
main .output h2 {
  font-size: 1rem;
  color: white;
}
main .output .person {
  font-size: 0.875rem;
  color: #7f9c9f;
}
main .output .flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.25rem;
}
main .output .number {
  font-size: 1.5625rem;
  font-weight: bold;
  color: #26c0ab;
  word-break: break-all;
}
main .output .btn-reset {
  display: block;
  width: 100%;
  border: none;
  outline: none;
  background: #26c0ab;
  color: #00494d;
  padding: 0.3125rem 0;
  text-transform: uppercase;
  font-family: "Space Mono", monospace;
  font-weight: bold;
  cursor: pointer;
  border-radius: 0.3125rem;
  transition: 250ms ease-in-out;
}
main .output .btn-reset:disabled {
  opacity: 0.3;
}
main .output .btn-reset:focus {
  outline: 1px solid white;
}
@media (min-width: 64em) {
  main .output .btn-reset {
    transform: translateY(8.4375rem);
  }
}